import React from 'react';
import styled from 'styled-components';

interface ISun {
    pos: number[];
    size: number;
    rot: number;
    time: number;
}

const SunBody = styled.div`	
	border-radius: 50%;
    position: absolute;
`;

const Star: React.FC<ISun> = ({pos, size, time, rot}) => {
    const loopSize = 7000;
    const loopFactor = 50;
    const perc = (time%loopSize)/loopSize;

    const massFactor = 15;
    return <SunBody style={{
    left: `calc(${pos[0]}vw - ${size * massFactor/2}px)`, 
    backgroundImage: `radial-gradient(circle at 50%, hsl(52, 80%,${45 + ((perc > 0.5 ? (1 - perc) * loopFactor : perc * loopFactor )/1.5)}%), 
    hsl(52, 80%, ${50 + (perc > 0.5 ? (0.5 * loopFactor) - ((perc - 0.5) * loopFactor) : perc * loopFactor)}%), hsl(2, 100%, 66%) )`,
    width: `${size * massFactor}px`, transform: `rotateY(-${rot}deg)`,
    height: `${size * massFactor}px`,
    zIndex: 6,
    top: `calc(${pos[1]}vh - ${size * massFactor/2}px)`}}>
        
    </SunBody>;
};

export default Star;
