import { createStore } from "redux";

const selectProject: string = 'SELECT-PROJECT';
const clearProject: string = 'CLEAR-PROJECT';

export const ACTIONS = {
    selectProject, clearProject
}

export type TinitialState = {
    selectedInfo: {
        name: string,
        description: string,
        link: string,
        index: number,
    }
}

const initialState: TinitialState = {
    selectedInfo: {
        name: '',
        description: '',
        link: '',
        index: -1,
    },
}

const main = (state: TinitialState = initialState, action: any) => {
    switch (action.type) {
        case ACTIONS.selectProject:
            return {
                selectedInfo: action.payload,
            }
        case ACTIONS.clearProject:
            return {
                ...state,
                ...initialState
            }
        default:
            return state;
    }
}



export default createStore(main);