import React, { useState } from 'react';
import styled from 'styled-components';

const BGStar = styled.div`
    position: absolute;
    background-color: hsl(265, 16%, 85%);
    width: 1px;
    border-radius: 50%;
    opacity: 0.7;
    height: 1px;
    &::after {
        width: 1px;
        content: '';
        border-radius: 50%;
        height: 1px;
        background-color: hsl(285,99%, 58%);
        left: 1px;
        opacity: 0.5;
        top: calc(50% - 0.5px);
        position: absolute;
    }
    // &::before {
    //     width: 1px;
    //     content: '';
    //     height: 1px;
    //     right: calc(50% - 0.5px);
    //     background-color: hsl(245, 56%, 44%);
    //     position: absolute;
    //     border-radius: 50%;
    //     opacity: 0.5;
    // }
`;

const BGContainer = styled.div`
    position: absolute;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    // background-color: hsl(245, 36%, 4%);
    background-image: linear-gradient(to bottom, 
        hsl(225, 46%, 2%), 
        hsl(225, 46%, 6%) 30%, 
        hsl(225, 46%, 6%) 60%, 
        hsl(15, 46%, 3%));
`;

const StarContainer = styled.div`
    width: 120vw;
    height: 100vh;
    position: absolute;    
    transform: rotate(-5deg);
    // filter: blur(1px);
`;


const MilkyWay: React.FC = () => {
    const [cluster] = useState(() => {
        const stars = [];
        const starCount = 500;
        const starSpread = 15;
        for (let i = 0; i < starCount; i++) {
            stars.push([
                ((i + 1) * 100)/starCount, 
                50 - Math.floor(Math.random() * starSpread * (Math.random() < 0.4 ? 4 : 1)) * (i%2 === 0 ? -1 : 1),
                1 + Math.random(),
                Math.floor(Math.random() * 100 * i) + 10,
                2500])
        }
        return stars;
    });
    return <BGContainer>
        <StarContainer>
        {
            cluster.map((e: number[]) => {
            return <BGStar style={{
                left: `${e[0]}%`, 
                top: `${e[1]}%`, 
                transform: `scale(${e[2]})`
            }} />})
        }
        </StarContainer>
    </BGContainer>
}

export default MilkyWay;