import React from 'react';
import CosmoObject from './CosmoObject';
import Star from './Star';
import MilkyWay from './MilkyWay';

interface ISolarSystem {
    navigation: number[];
    time: number;
    scale: number;
}

const SolarSystem: React.FC<ISolarSystem> = ({navigation, time, scale}) => {
    // const planetSize = 4;
    const [planetMap, setPlanetMap] = React.useState(() => {
        const map: any | object[] = [];
        const centralPoint = [50, 50];
        // const RandomFromArray = (array: any[]) => array[Math.floor(Math.random() * array.length)];
        // const names = ['Mar', 'Ter', 'Merc', 'Jup', 'Plut', 'Ioc', 'Inav', 'V', 'Ac'];
        // const subnames = ['ra', 'ete', 'ate', 'Ã£o', 'inho', 'ika', 'impa', 'imceq', 'iter', 'iner', 'amer'];       
        // const planetTypes = ['Rochoso', 'Gasoso'];  
        const projects = ['Outrun CSS', 'Underwater CSS Marketplace', 'Explore Gallery', 'Food delivery template'];
        const plinks = ['https://outrun-challenge.herokuapp.com/', 
        'https://marketplace-template.herokuapp.com/', 
        'https://sample-portfolio-brainclouder.herokuapp.com/explore', 'https://native-food-delivery.herokuapp.com/'];
        const description = ['Outrun-style game', `A marketplace template I've made some time ago.`, 
        `Another template, this one I was messing with mouse position and parallax.`, 
        `This is a laboratory I've made to study React Native through React Native Web library. This project may
        lack the proper optimization, but it gave me insights on how Redux affects performance,
        `];

        for (let i = 0; i < projects.length ; i++) {            
            const randomType = ['Rochoso', 'Gasoso'];      
            // const randomName = RandomFromArray(names) + RandomFromArray(subnames);
            // const randomCoordinates = [ 40 - ((i + 1) * 10), 40];
            // const randomMass = (Math.random() * 1) + 0.5
            const isRocky = i !== projects.length - 1;
            const randomSatellite = isRocky ? Math.floor(Math.random() * 3) : Math.floor(Math.random() * 5) + 2;
            const satelliteArray = [];
            for (let o = 0; o < randomSatellite; o++) {
                isRocky ? satelliteArray.push([((o + 2) * 15) + Math.floor(Math.random() * 15), (Math.random() * 0.5) + 0.5])
               : satelliteArray.push([(o + 1) * 10 + Math.floor(Math.random() * 10) + 10, 0.3]);
            }

            map.push({
                name: projects[i],
                massCenter: centralPoint,
                distance: ((i) * 60),
                description: description[i],
                link: plinks[i],
                satellite: satelliteArray,
                index: i,
                type: isRocky ? 0 : 1
                
            });
        }
        return map;
    });
    const massCenterCoordinates = [50 + (navigation[0]/2), 50 - (navigation[1]/2)];
    // const timeRotateFactor = (time%4000)/4000;

    const rotateStyle = 60;

    return <>
    <MilkyWay/>
    <div style={{transform: `scale(${(window.innerWidth < 835 ? window.innerWidth/835 : 1) + rotateStyle/180}) rotateX(${rotateStyle}deg) translateZ(-${rotateStyle}px)`}}>
        <div>
        {/* <CosmoObject coordinates={[60, 40]} navigation={navigation}/> */}
        {
            planetMap.map((e: any, i: number) => <CosmoObject rot={rotateStyle} type="planet" key={e.name + i} 
            distance={e.distance + ((scale*10)/2)} size={scale} info={e}
            navigation={navigation} center={massCenterCoordinates} time={time + 65000} /> )
        }
        <Star size={scale} time={time} rot={rotateStyle} pos={massCenterCoordinates}/>
        {/* <div style={{
            backgroundColor: 'yellow',
            borderRadius: '50%',
            width: '50px',
            height: '50px',
            position: 'absolute',
            left: `calc(50vw - 25px + ${navigation[0]/2}vw)`,
            top: `calc(50vh - 25px - ${navigation[1]/2}vh)`,
        }}>

        </div> */}
        </div>
    </div>    
    </>
}

export default SolarSystem;